@import "sass/index";

.currencyWrapper {
  max-width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 102px;
  flex-wrap: wrap;
  padding: spacing(2);

  background: use(--info-main);
  border-radius: spacing(1.25);

  padding-left: spacing(1.5);
  padding-right: spacing(3);

  &:not(:first-child) {
    margin-top: 10px;
  }

  @media (max-width: $mobileScreen) {
    padding-right: spacing(1.5);
  }
}
.labelWrapper {
  min-width: spacing(16);
}
.label {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 16px;
  color: use(--template-modal-title-color);
  line-height: 28px;
  line-spacing: 1.07px;
}
.value {
  font-family: "Gilroy";
  font-size: 16px;
  color: use(--secondary-main);
  line-height: 28px;
  line-spacing: 1.07px;

  max-width: 110;
  text-overflow: ellipsis;
  overflow: hidden;

  @media (max-width: $mobileScreen) {
    margin-right: spacing(4);
    font-size: 20px;
    color: use(--palette-secondary-main);
    line-height: 28px;
    line-spacing: 1.33px;
    max-width: 175px;

    @media (max-width: 390px) {
      max-width: 120px;
    }
    @media (max-width: 360px) {
      padding-right: spacing(4);
      margin: 0;
    }
  }
}
.buttonWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  align-items: center;
  justify-content: center;

  @media (max-width: $mobileScreen) {
    width: 100%;
  }
}
.cell {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.leftBlock {
  display: flex;
  flex-direction: row;

  @media (max-width: $mobileScreen) {
    width: 100%;
  }
}
.button {
  padding: 6px;
  margin: spacing(0.75);
}
.grow {
  flex: 1 1 auto;
}

.iconWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: spacing(1.5);

  height: spacing(7);
  width: spacing(7);
}
.icon {
  height: spacing(3);
  width: spacing(3);
}
