$boxHeight: 200px;
$background: #ffffff;

.root {
  padding: 5px 10px;

  & > div .text {
    text-align: left;
  }
}
.box {
  padding: 10px;
  background: $background;
  height: $boxHeight;
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.text {
  white-space: nowrap;

  color: black !important;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0;
}
.textId {
  white-space: nowrap;
  margin: 5px 0;
  color: black !important;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1rem;
  font-weight: bold;
}
.title {
  margin: 0;
  margin-left: 10px;

  color: black;
  text-align: left;
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.url {
  text-decoration: none;
  color: black;
}

.bLabel {
  margin: 0;
  display: inline;
  padding: 0.2em 0.6em 0.3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: left;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;
}
.bDefault {
  background: #777;
}
.bPrimary {
  background: #337ab7;
}
.bSuccess {
  background: #5cb85c;
}
.bInfo {
  background: #5bc0de;
}
.bWarning {
  background: #f0ad4e;
}
.bDanger {
  background: #d9534f;
}
