@import "sass/index";

.root {
  color: rgba(white, 0.5);
  width: 100%;
  height: 100%;
  position: relative;
  min-height: 300px;
}
.table {
}
.tabBody {
}
.tRow {
}

.tCell {
  position: relative;
  width: max-content;
  white-space: nowrap;
  color: rgba(white, 0.5);
  border: none;
  max-width: 160px;

  text-align: center;

  @media (max-width: 370px) {
    padding: 16px 5px;
  }
}
.headCell {
  text-align: center;
  font-size: 0.875rem;
  margin: 0;
}
.headFlex {
  display: flex;
  justify-content: center;
  align-items: center;

  line-height: 110%;
  width: min-content;
  margin: 0 auto;

  @media (max-width: $mobileScreen) {
    font-size: 0.7rem;
  }
}
.tHead {
  font-weight: bold;
}

.tStatus {
  height: 15px;
  width: 15px;
  background: white;
  border-radius: 50%;
  position: absolute;
  left: 0;
}
.tSuccess {
  background: use(--subsidiary-green);
}
.tFail {
  background: use(--subsidiary-red);
}

.cellWrapper {
  position: relative;
  display: flex;
  align-items: center;
}
.amount {
  margin: 0;
  margin-left: 30px;
  padding: 0;
  width: max-content;
  max-width: 70px;
  overflow: auto;

  &::-webkit-scrollbar {
    width: 0;
    display: none;
  }
}
.url {
  color: rgba(white, 0.5);
  font-weight: bold;
  text-decoration: none;
}

.bLabel {
  display: inline;
  padding: 0.2em 0.6em 0.3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;
}
.bDefault {
  background: #777;
}
.bPrimary {
  background: #337ab7;
}
.bSuccess {
  background: #5cb85c;
}
.bInfo {
  background: #5bc0de;
}
.bWarning {
  background: #f0ad4e;
}
.bDanger {
  background: #d9534f;
}
.helpWrapper {
  position: relative;
}
.icon {
  color: rgba(255, 255, 255, 0.65);
  cursor: pointer;
  margin: 0 auto;
  display: block;
}

.closeWrapper {
  position: fixed;
  top: 0;
  background: transparent;

  height: 100%;
  width: 100%;
  z-index: 2;
}

.coinIcon {
  position: static;
  margin-right: 5px;
  z-index: -1;
}

.popper {
  margin-top: -100px;
}
