$boxHeight: 110px;
$background: #ffffff;

.root {
  // position: absolute;
  // bottom: 100%;
  // left: 0;
  // z-index: 5px;
  // min-width: 150px;

  & > div .text {
    text-align: left;
  }
}
.box {
  padding: 10px;
  background: $background;
  height: $boxHeight;
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.text {
  white-space: nowrap;
  margin: 5px 0;
  color: black !important;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1rem;
  font-weight: bold;
}
.title {
  margin: 0;
  margin-left: 10px;
  color: black;
  text-align: left;
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.triangle {
  position: absolute;
  bottom: -2px;
  left: 40px;
  width: 0;
  height: 0;
  border-top: 10px solid $background;
  border-right: 10px solid transparent;
  transform: rotate(-135deg);

  @media (max-width: 600px) {
    display: none;
  }
}
.triangleWrap {
  left: -3px;
  bottom: 20px;
  transform: rotate(-45deg);
}
.url {
  text-decoration: none;
  color: black;
}
