@import "sass/index";

$header-height: 81px;
$select-height: 71px;
$buttons-height: 89px;
$padding: spacing(4);
$rootSpacing: spacing(8);

$pagination-background: white;
$pagination-darkness: rgb(234, 234, 234);

.root {
  color: white;
  width: 100%;
  max-height: MIN(
    calc(
      100vh - #{$header-height} - #{$select-height * 2} - #{$buttons-height}
    ),
    540px
  );
  position: relative;
}
.swipe {
  width: 100%;
  overflow-x: auto;
  position: relative;

  &::-webkit-scrollbar {
    display: none;
  }
}
.swipeUnit {
  position: relative;
  left: 0;
  top: 0;
  transition: transform 0.5s;
}
.swipeDisable {
  position: absolute;
  transform: translate(-200%, 0);
}
.paginationBlock {
  width: 100%;
  height: 40px;

  display: flex;
  justify-content: flex-end;
  margin-bottom: 15px;
}
.pagination {
  &_root {
    color: rgba($pagination-background, 0.5);
  }

  &_button {
    color: rgba($pagination-background, 0.5);

    background: rgba($pagination-background, 0);

    &:hover {
      background-color: rgba($pagination-darkness, 0.2);
    }
    &:focus {
      background-color: rgba($pagination-darkness, 0.2);
    }
    &:active {
      background-color: rgba($pagination-darkness, 0.2);
    }
  }
  &_active {
    color: #090c16;
    background: $pagination-background !important;
    opacity: 1 !important;

    &:hover {
      background-color: $pagination-darkness !important;
    }
    &:focus {
      background-color: $pagination-darkness !important;
    }
    &:active {
      background-color: $pagination-darkness !important;
    }
  }
}
.paginationLoading {
  & button {
    opacity: 0.3;
  }
  & div {
    opacity: 0.3;
  }
}
.tabIndicator {
  background-color: white;
  align-self: flex-end;
  height: 2px;
}
.tab {
  background-color: transparent !important;
  padding: 12px 36px;
  font-size: 0.875rem;
  font-family: "Gilroy";
  font-weight: 500;
  line-height: 1.75;
  color: inherit;

  &:disabled {
    opacity: 1;
  }
}
